<template>
    <div class="users">
      <el-header>
        <!--头部用户查询-->
        <el-input
          placeholder="请输入内容"
          v-model="content"
          clearable>
        </el-input>
        <el-button type="primary" @click="findUsers(content)">查询</el-button>
        <!-- 导入用户的弹框 Form -->
        <el-button type="primary" @click="FormVisible = true">导入用户</el-button>

        <el-dialog title="编辑" :visible.sync="FormVisible">
          <el-form :model="formData">
            <el-form-item label="用户名" :label-width="formLabelWidth" :filter-method="findUsers">
              <el-input v-model="formData.userName" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="证件号" :label-width="formLabelWidth">
              <el-input v-model="formData.idCard" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="手机号" :label-width="formLabelWidth">
            <el-input v-model="formData.phone" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="密码" :label-width="formLabelWidth">
              <el-input v-model="formData.pwd" auto-complete="off"></el-input>
            </el-form-item>
            <div class="upload">
              <span>图片</span>
              <el-upload
                class="avatar-uploader"
                action="https://upload-z1.qiniup.com"
                :show-file-list="false"
                :on-success="upload"
                :data="{token:token}"
              >
                <img v-if="formData.avatar" :src="formData.avatar" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </div>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="FormVisible = false">取 消</el-button>
            <el-button type="primary" @click="addUsers">确 定</el-button>
          </div>
        </el-dialog>
        <!--头部的设置-->
        <el-dropdown>
          <i class="el-icon-setting" style="margin-right: 15px"></i>
          <span>设置</span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>查看</el-dropdown-item>
            <el-dropdown-item>新增</el-dropdown-item>
            <el-dropdown-item>删除</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-header>
      <!--表格-->
      <el-main>
        <el-table
          :data="tableData"
          stripe
          style="width: 100%"
          @selection-change="selectionChange"
        >
          <el-table-column
            prop="select"
            label=""
            width="50"
            show-overflow-tooltip
            type="selection"
          >
          </el-table-column>
          <el-table-column
            prop=""
            label="#"
            type="index"
            :index="formatter"
            width="100"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="userName"
            label="用户名"
            width="150"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="idCard"
            label="证件号"
            width="180"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="phone"
            label="手机"
            width="180"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="score"
            label="积分"
            width="180"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                @click="edit(scope.row._id)">编辑</el-button>
              <el-button
                size="mini"
                type="danger"
                @click="del(scope.row._id)">删除</el-button>
              <el-button
                size="mini"
                type="success"
                v-if="scope.row.isCanLogin == false"
                @click="open(scope.row._id)">启用</el-button>
              <el-button
                size="mini"
                type="danger"
                v-if="scope.row.isCanLogin == true"
                @click="close(scope.row._id)">禁用</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-main>
      <!--底部-->
      <el-footer>
        <el-button type="primary" @click="toggleSelection(multiSelection)">批量重置密码</el-button>
        <el-pagination
          background
          layout="prev, pager, next,jumper"
          :total="total"
          :current-page="page"
          :page-size="pageSize"
          @current-change="pageChange"
        >
        </el-pagination>
      </el-footer>
    </div>
</template>
<script>
    export default{
      name: "users",
      data(){
            return{
              tableData:[],
              content:'',
              //选择器
              multiSelection:[],
              userId:[],
              //弹框
              FormVisible: false,
              formData: {
                userName: '',
                idCard:'',
                phone:'',
                pwd:'',
                avatar:''
              },
              formLabelWidth: '120px',
              //token
              token:'',
              //分页器
              pageSize:5,
              page:1,
              total:10
            }
      },
      methods:{
          //查询
        findUsers(id, row, column){
          this.$axios.get('getUsers',{id}).then( res => {
              console.log(res.data)
//              this.tableData.push(res.data);
              this.tableData = res.data
//              console.log(this.tableData)
          })
        },
        //添加用户
        getToken(){
          this.$axios.qiniuGet().then( res => {
            this.token =res.data
          })
        },
            //数据回传
        upload(res){
          this.formData.avatar = res.url
        },
            //添加用户
        addUsers() {
          this.$axios.post('addUsers',this.formData).then( res => {
              if(res.code == 200){
                  this.$message({
                    type:'success',
                    message:'添加成功'
                  });
                setTimeout(() => {
                  this.$router.go(0)
                },100)
              }else{
                  this.$message({
                    type:'error',
                    message:'添加失败'
                  });
              }
          })
        },
        //获取用户列表
        getUsers(){
          this.$axios.get('getUsers').then( res => {
//              console.log(res.data)
            this.tableData = res.data
//            for(var i=0;i<this.tableData.length;i++){
//              this.num +=1
//              this.tableData.push(this.num)
//            }
          })
        },
        //编辑
        edit(id){
//            console.log(id)
          this.$router.push({path:'/users/update',query:{id} })
        },
        //删除一个
        del(id){
//          console.log(id)
          this.$confirm('此操作将永久删除该内容, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$axios.post('delUserOne', {id}).then(res => {
              if(res.code == 200){
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
                setTimeout( () => {
                  this.$router.go(0)
                },1000)
              }else{
                this.$message({
                  type: 'error',
                  message: '删除失败!'
                });
              }
            })
          }).catch(() => {
            this.$message({
              type: 'warning',
              message: '已取消删除'
            });
          })
        },
        //批量选择
        selectionChange(val){
          this.multiSelection = val
//          console.log(val)
        },
        toggleSelection(rows){
          if(rows){
              for(var key in rows){
//                  console.log(key)
//                this.userId = rows[key]._id
                this.userId.push(rows[key]._id)
              }
//            console.log(this.userId)
            this.$axios.post('updateManyPwd', {userId:this.userId}).then( res => {
              if(res.code == 200) {
                this.$message({
                  type:'success',
                  message:'批量修改成功'
                })
              }
            })
          }else {
            this.$message({
              type:'warning',
              message:'没有选择'
            })
          }
        },
        //启用和禁用
        open(id){
          this.$axios.post('isCanLogin',{isCanLogin:true, id}).then( res => {
              if(res.code == 200){
                  this.$message({
                    type:'success',
                    message:'权限已开启'
                  })
                setTimeout(() => {
                      this.$router.go(0)
                },1000)
              }else {
                this.$message({
                  type:'danger',
                  message:'开启失败'
                })
              }
          })
        },
        close(id){
          this.$axios.post('isCanLogin',{isCanLogin:false, id}).then( res => {
            if(res.code == 200){
              this.$message({
                type:'success',
                message:'权限已禁用'
              })
              setTimeout(() => {
                this.$router.go(0)
              },1000)
            }else {
              this.$message({
                type:'danger',
                message:'禁用失败'
              })
            }
          })
        },
        //底部分页器
        //这个时候利用formatter就可以实现索引累加啦
        formatter(index){
            //放回索引
          return index + (this.page -1) *this.pageSize +1
        },
        pageChange(val){
//          console.log(`当前页: ${val}`);
          this.page = val
          this.$axios.get('getUsers',{page:val, pageSize:this.pageSize}).then( res => {
//              setTimeout( () => {
//                this.$router.go(0)
//              },1000)
              this.tableData = res.data
          })
        },

        //获取数据库文档总数
        getNumber(){
          this.$axios.get('getUsersNum').then( res => {
              this.total = res.data
          })
        }
      },
      created(){
          this.getUsers();
          this.getToken();
          this.getNumber()
      }
    }
</script>
<style>
  .users {
    width: 100%;
    padding-top: 2px;
    padding-left: 20px;
  }
  /*头部*/
  .users .el-header{
    position: relative;
    height: 78px !important;   /*element.style里的样式需要用!important来修改*/
    margin-right: 22px;
    padding-left:11px;
    text-align: left;
    background-color: #f1f1f1;
  }
  .users .el-header .el-input{
    width: 190px;
    margin-top: 10px;
  }
  .users .el-header .el-input .el-input__inner{
    border:1px solid #c2cdda;
  }
  .list .el-header .el-button{
    margin-left:14px;
  }
  .users .el-header .el-dropdown{
    position:absolute;
    top:10px;
    right:10px;
    font-size:22px;
  }
  .users .el-header .el-dropdown i{
    font-size:22px;
    margin:0 !important;
  }
  .users .el-header .el-dropdown span{
    font-size:14px;
    line-height: 26px;
  }
  /*内容区*/
  .users .el-main{
    margin:10px 22px 10px 0;
    padding: 0 14px;
    border:1px solid #dfe7ed;
  }
  .users .el-table .has-gutter .cell{
    color:#000;
  }
  /*弹框*/
  .users .el-header .el-form-item .el-input{
    margin: 0;
  }
  /*表单上传*/
  .upload{
    margin-left: 78px;
  }
  .upload span{
    padding-right: 12px;
  }
  .avatar-uploader{
    display: inline-block;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
      /*设置上传图片的大小*/
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  /*底部*/
  .users .el-footer{
    /*position: fixed;*/
    /*min-height: 500px;*/
    /*bottom: 20px;*/
    /*right:20px;*/
    position:relative;
    height: 56px !important;
    margin:0 22px 0 0;
    padding:0;
    background-color: #f1f1f1 !important;
  }
  .users .el-button{
    margin:10px 0 0 10px;
  }
  .users .el-footer .el-pagination{
    display: inline-block;
    position:absolute;
    top:12px;
    right:15px;
  }
  .users .el-footer .el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li{
    background-color: #ffffff;
  }
</style>
<style scope>

</style>



